<template>
    <div>
        <component
            v-for="(item, key) in items"
            :key="key"
            :index="key"
            :is="item.type"
            :opts="item">
        </component>
    </div>
</template>

<script>
export default {
    props: ['data'],
    data() {
        return {
            items: null
        };
    },
    beforeMount: function () {
        if (this.data != null) {
            this.items = Object.assign({}, this.data);
        }
    }
}
</script>
