        computed: {
            filteredDataArray() {
                return this.data.filter((option) => {
                    return option
                        .toString()
                        .toLowerCase()
                        .indexOf(this.name.toLowerCase()) >= 0
                })
            }
        },
        watch: {
            items() {
                this.code = Object.assign({}, this.items);
            }
        },
        methods: {
            getFilteredTags(text) {
                this.filteredTags = this.blocks.filter((option) => {
                    return option
                        .toString()
                        .toLowerCase()
                        .indexOf(text.toLowerCase()) >= 0
                })
            },
            update: debounce(function () {
                this.items = Object.assign({}, this.items);
            }, 200),
            reset() {
                if (this.opts != null) {
                    this.items = Object.assign({}, this.opts);
                }
            },
            typing(text) {
                this.name = text;
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = Object.assign({}, this.opts);
            }
