<template>
    <footer class="footer">
        <s-columns v-if="items.blocks" :opts="items"></s-columns>
        <s-content :opts="items"></s-content>
    </footer>
</template>

<script>
    export default {
        props: ['opts'],
        data() {
            return {
                items: {}
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = Object.assign({}, this.opts);
            }
        }
    }
</script>