<template>
    <div style="padding: 0.75rem">
        <b-field label="Page Title">
            <b-input maxlength="100" placeholder="Specify a page title for display on the browser tab" v-model="items.title" @input="update"></b-input>
        </b-field>
        <b-field label="Description">
            <b-input maxlength="173" type="textarea" placeholder="Provide a brief description of this page for search engines to display on their results page" v-model="items.description" @input="update"></b-input>
        </b-field>
        <b-field label="Path">
            <b-input maxlength="100" placeholder="Specify a path e.g. /about-us" v-model="items.path" @input="update"></b-input>
        </b-field>
        <b-field label="Blocks">
            <b-taginput
            v-model="items.data"
            :data="filteredTags"
            autocomplete
            field="slug"
            icon="label"
            placeholder="Search for blocks to add to this path"
            @typing="getAsyncData">
            </b-taginput>
        </b-field>
        <input class="button is-danger" type="reset" @click="reset">
        <s-code :opts="code" :type="'s-page'"></s-code>
    </div>
</template>

<script>
    import debounce from 'lodash/debounce'
    export default {
        props: ['opts'],
        data() {
            return {
                code: {},
                items: {},
                filteredTags: [],
                isSelectOnly: false,
                types: null,
                data: []
            }
        },
        watch: {
            items() {
                this.code = Object.assign({}, this.items);
            }
        },
        methods: {
            reset() {
                if (this.opts != null) {
                    this.items = Object.assign({}, this.opts);
                }
            },
            update: debounce(function () {
                this.items = Object.assign({}, this.items);
            }, 200),
            store () {
                this.$http.post(
                    '/api/path', 
                    this.code,
                    {
                        headers: {
                            'Content-Type': 'application/json',
                        }
                    }
                ).then(response => {
                    this.$toast.open(response.data.response)
                }).catch((error) => {
                    throw error
                });
            },
            getFilteredTags(text) {
                this.filteredTags = this.data.filter((option) => {
                    return option.user.first_name
                        .toString()
                        .toLowerCase()
                        .indexOf(text.toLowerCase()) >= 0
                })
            },
            getAsyncData: debounce(function (text) {
                this.data = []
                this.isFetching = true
                this.$http.get(`/api/blocks?query=${text}`)
                    .then(({ data }) => {
                        this.filteredTags = [];
                        data.results.forEach((item) => this.filteredTags.push(item))
                        this.isFetching = false
                    })
                    .catch((error) => {
                        this.isFetching = false
                        throw error
                    })
            }, 500)
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = Object.assign({}, this.opts);
            }
        }
    }
</script>
