<template>
    <div>
        <div class="notification" :class="currentClass" v-for="(item, index) in items" v-bind:key="index">
            {{ item.title }}
            <b-field label="Enter a top level page">
                <b-taginput
                    v-model="item.blocks"
                    :data="filteredTags"
                    autocomplete
                    field="title"
                    icon="label"
                    placeholder="Add a page"
                    @typing="getAsyncData">
                </b-taginput>
            </b-field>
            <s-taxonomy v-if="item.blocks && typeof item.blocks[0].title != 'undefined'" :opts="item.blocks" :count="count + 1" :demo="demo"></s-taxonomy>
        </div>
    </div>
</template>

<script>
    const pages = [{"title": "Social", "dropdown": true, "hoverable": true}, {"title": "Contact Us", "link": "contact"},{"title": "Twitter", "link": "https://twitter.com"},{"title": "Instagram", "link": "https://instagram.com"},{ "title": "Facebook", "link": "https://facebook.com"}, {"title": "Divider", "divider": true}]
    import debounce from 'lodash/debounce'
    export default {
        props: ['opts', 'count', 'demo'],
        data() {
            return {
                currentClass: 'is-primary',
                items: {},
                filteredTags: [],
                classes: ['is-danger', 'is-info', 'is-warning', 'is-success']
            }
        },
        methods: {
            getFilteredTags(text) {
                let filteredData = pages.filter((option) => {
                    return option.title
                        .toString()
                        .toLowerCase()
                        .indexOf(text.toLowerCase()) >= 0
                })
                this.filteredTags = [];
                filteredData.forEach((itemReturned) => {this.filteredTags.push(Object.assign({}, itemReturned))})
            },
            getAsyncData: debounce(function (text) {
                if (this.demo) {
                    return this.getFilteredTags(text)
                }
                this.data = []
                this.isFetching = true
                this.$http.get(`/api/paths?query=${text}`)
                    .then(({ data }) => {
                        this.filteredTags = [];
                        data.results.forEach((item) => this.filteredTags.push(item))
                        this.isFetching = false
                    })
                    .catch((error) => {
                        this.isFetching = false
                        throw error
                    })
            }, 500)
        },
        beforeMount() {
            if (this.opts != 'undefined') {
                this.items = this.opts;
            }
            this.currentClass = this.classes[this.count % 4];
        }
    }
</script>
