<template>
    <b-field
        style="padding: 1.5rem"
        :grouped="items.grouped"
        :label="items.title"
        :type="items.colour"
        :position="items.position"
        :message="items.description">
        <b-input
            v-model="items.value"
            :name="items.name"
            :custom-class="items.customclass"
            :expanded="items.expanded"
            :has-counter="items.counter" 
            :size="items.size"
            :placeholder="items.subtitle"
            :icon-pack="items.iconpack"
            :icon="items.icon"
            :loading="items.loading"
            :required="items.required"
            :disabled="items.disabled"
            :rounded="items.rounded"
            :type="items.subtype"
            :min="items.minValue"
            :max="items.maxValue"
            :minlength="items.minLength"
            :maxlength="items.maxLength"
            :password-reveal="items.passwordReveal"
            @input="input">
        </b-input>
         <p v-if="items.button" class="control">
            <button class="button" :class="[items.colour, items.size]">{{ items.button }}</button>
        </p>
    </b-field>
</template>

<script>
    export default {
        props: ['opts'],
        data() {
            return {
                items: {},
                type: 'range'
            }
        },
        methods: {
            input(text) {
                if (this.items.subtype == 'range') {
                    this.items.description = text;
                }
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = Object.assign({}, this.opts);
            }
        }
    }
</script>
