<template>
    <div v-if="items.blocks" class="columns" :class="columns">
        <div class="column" v-for="(item, index) in items.blocks" v-bind:key="index" :class="[item.columnSize, item.columnOffset, {'is-narrow': item.narrow}, item.visible]">
            <s-component
                :key="index"
                :is="item.type"
                :opts="item">
            </s-component>
        </div>
    </div>
</template>

<script>
    export default {
        props: ['opts'],
        data() {
            return {
                items: {},
                defaults: {}
            }
        },
        computed: {
            columns() {
                return [
                    {'is-multiline': this.items.multiline || this.defaults.multiline }, 
                    {'is-gapless': this.items.gapless || this.defaults.gapless}, 
                    {'is-centered': this.items.centred || this.defaults.centred }, 
                    {'is-vcentered': this.items.verticallyCentred || this.defaults.verticallyCentred }, 
                    {'is-variable': this.items.variableGap || this.defaults.variableGap },
                    this.items.visible || this.visible,
                    this.items.mobileGapSize || this.defaults.mobileGapSize,
                    this.items.tabletGapSize || this.defaults.tabletGapSize,
                    this.items.desktopGapSize || this.defaults.desktopGapSize,
                    this.items.widescreenGapSize || this.defaults.widescreenGapSize,
                    this.items.fullhdGapSize || this.defaults.fulldhGapSize
                ];
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = Object.assign({}, this.opts);
            }
        }
    }
</script>
