<template>
    <section class="hero" :class="hero">
        <div v-if="items.navbar" class="hero-head">
            <s-navbar></s-navbar>
        </div>
        <div class="hero-body">
            <s-content :opts="items"></s-content>
        </div>
        <div v-if="items.blocks" class="hero-foot">
            <s-component
                v-for="(item, index) in items.blocks"
                :key="index"
                :is="item.type"
                :opts="item">
            </s-component>
        </div>
    </section>
</template>

<script>
    export default {
        props: ['opts'],
        data() {
            return {
                items: {},
                defaults: {}
            }
        },
        computed: {
            hero() {
                return [
                    {'is-bold': this.items.bold || this.defaults.bold },
                    this.items.colour || this.defaults.colour,
                    this.items.size || this.defaults.size
                ];
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = Object.assign({}, this.opts);
            }
        }
    }
</script>
