<template>
    <div style="padding: 0.75rem" :class="items.fontAlignment">
        <h1 class="title">{{ items.title }}</h1>
        <h2 class="subtitle">{{ items.subtitle }}</h2>
        <div class="content" v-html="items.content"></div>
    </div>
</template>

<script>
    export default {
        props: ['opts'],
        data() {
            return {
                items: {}
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = Object.assign({}, this.opts);
            }
        }
    }
</script>
