<template>
  <div style="padding: 0.75rem">
        <s-navbar :opts="code.blocks"></s-navbar>
        <b-field label="Taxonomy Name">
            <b-input maxlength="100" placeholder="Please specify a name for this taxonomy" v-model="items.name" @input="update"></b-input>
        </b-field>
        <b-field label="Start typing the name of a page you want to add to your menu...">
            <b-taginput
                v-model="items.blocks"
                :data="filteredTags"
                autocomplete
                :allow-new="allowNew"
                field="title"
                icon="label"
                placeholder="Search Pages"
                @typing="getAsyncData">
            </b-taginput>
        </b-field>
        <s-taxonomy v-if="items.blocks" :opts="items.blocks" :demo="demo" :count="0"></s-taxonomy>
        <button v-if="!demo" class="button is-medium" @click="store">Save Taxonomy</button>
        <s-code :opts="code" :type="'s-navigation'"></s-code>
  </div>
</template>

<script>
    const pages = [{"title": "Social", "dropdown": true, "hoverable": true}, {"title": "Contact Us", "link": "contact"},{"title": "Twitter", "link": "https://twitter.com"},{"title": "Instagram", "link": "https://instagram.com"},{ "title": "Facebook", "link": "https://facebook.com"}, {"title": "Divider", "divider": true}]
    import debounce from 'lodash/debounce'
    export default {
        props: ['taxonomy', 'opts'],
        data() {
            return {
                code: {},
                demo: true,
                path: '/admin/pages/fetch',
                items: {},
                filteredTags: [],
                isSelectOnly: false,
                allowNew: false
            }
        },
        watch: {
            items() {
                let temp = Object.assign({}, this.items);
                delete temp.demo;
                this.code = temp;
            }
        },
        methods: {
            update: debounce(function () {
                this.items = Object.assign({}, this.items);
            }, 100),
            store () {
                this.$http.post(
                    '/api/paths', 
                    this.code,
                    {
                        headers: {
                            'Content-Type': 'application/json',
                        }
                    }
                ).then(response => {
                    this.$toast.open(response.data.response)
                }).catch((error) => {
                    throw error
                });
            },
            getFilteredTags(text) {
                this.filteredTags = pages.filter((option) => {
                    return option.title
                        .toString()
                        .toLowerCase()
                        .indexOf(text.toLowerCase()) >= 0
                })
            },
            getAsyncData: debounce(function (text) {
                if (this.demo) {
                    return this.getFilteredTags(text)
                }
                this.data = []
                this.isFetching = true
                this.$http.get(`/api/paths?query=${text}`)
                    .then(({ data }) => {
                        this.filteredTags = [];
                        data.results.forEach((item) => this.filteredTags.push(item))
                        this.isFetching = false
                    })
                    .catch((error) => {
                        this.isFetching = false
                        throw error
                    })
            }, 500)
        },
        beforeMount() {
            if (this.opts !== null) {
                this.items = this.opts;
            }
            if (this.demo) {
                this.demo = true;
            }
        }
    }
</script>
