<template>
    <div class="code-wrapper">
        <span class="click-to-copy" :class="{'copying': copying}" @click="copy">Copy</span>
        <div class="language">
            <pre class="code" v-html="items"></pre>
        </div>
    </div>
</template>

<script>
  export default {
    props: ['opts', 'type'],
    data() {
      return {
          items: {},
          copying: false
      }
    },
    watch: {
        opts() {
            this.items = Object.assign(this.opts, {});
            delete this.items.block;
            this.items.type = this.type;
        }
    },
    methods: {
        copy() {
            this.copying = true;
            var textArea = document.createElement("textarea");
            textArea.value = JSON.stringify(this.items);
            document.body.appendChild(textArea);
            textArea.focus();
            textArea.select();
            try {
                document.execCommand('copy');
                setTimeout(() => {this.copying = false;}, 500);
            } catch (err) {
                // eslint-disable-next-line
                console.log(err);
            }
            document.body.removeChild(textArea);
        }
    }
  }
</script>

<style>
    pre {
        line-height: 1.4;
        padding: 1.25rem 1.5rem;
        margin: .85rem 0;
        background-color: #282c34;
        border-radius: 6px;
        overflow: auto;
        color: white;
    }
    .language {
        position: relative;
    }
    .language:before {
        content: "JSON";
    }
    .language:before {
        position: absolute;
        z-index: 3;
        top: .8em;
        right: 1em;
        font-size: .75rem;
        color: hsla(0,0%,100%,.4);
    }
    .code-wrapper {
        margin-top: 0.75rem;
        display: block;
    }
    .code-wrapper:hover .click-to-copy {
        display: block;
    }
    .click-to-copy {
        display: none;
    }
    .copying::after {
        transition: fadeOut 4s linear forwards;
        animation: floatup .5s linear forwards;
    }
    @keyframes fadeOut {
        0% { opacity:1; }
        100% { opacity:0; } 
    }
    @keyframes floatup {
        0% { transform: translate3d(-90%,0,0); }
        100% { transform: translate3d(-90%,-10px,0); }
    }
    .copying::after {
        content: "Copied";
        color: #0069ff;
        display: inline-block;
        position: absolute;
        top: -2px;
        transform: translate3d(-90%,0,0);
        white-space: nowrap;
    }
    .click-to-copy {
        opacity: .001;
    }
    .click-to-copy {
        color: #0069ff;
        font-size: 13px;
        line-height: 100%;
        position: relative;
        opacity: .999;
        transition: opacity .2s ease-in-out;
        top: -1px;
    }
    .click-to-copy {
        cursor: pointer;
    }
</style>
